<template>
	<div class="pdawrap">
		<div class="pda_selected_div">
				<span class="searchout_title" >{{$t('pda.onlycode')}}:</span>
				<el-input v-model="label_code" clearable @keyup.enter.native ="takeIn" class="pdasearch_input"></el-input>
			</div>
			<el-button type="primary" icon="el-icon-search" class="pdasearch_btn" @click="search" :disabled="isDisabled?true:false">{{$t('common.search')}}</el-button>		
		<div class="table_div" v-if="mate_list">
			<table class="labeltable">
				<tr class="label_tr">
          <td class="title_td">{{$t('storage.label_code')}}</td>
          <td class="content_td">{{mate_list.label_code}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.customer_name')}}</td>
          <td class="content_td">{{mate_list.customer}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.supplier_name')}}</td>
          <td class="content_td">{{mate_list.supplier_name}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.mate_type')}}</td>
          <td class="content_td">{{mate_list.mate_type}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.part_num')}}</td>
          <td class="content_td">{{mate_list.part_num}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.shelf_id')}}</td>
          <td class="content_td">{{mate_list.shelf_id}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.position')}}</td>
          <td class="content_td">{{mate_list.position}}</td>
        </tr>
        <tr>
          <td class="title_td">{{$t('storage.description')}}</td>
          <td class="content_td">{{this.mate_list.description}}</td>
        </tr>
         <tr>
          <td class="title_td">{{$t('storage.mfg_date')}}</td>
          <td class="content_td">{{this.mate_list.mfg_date}}</td>
        </tr>
			</table>	
		</div>
	</div>
</template>
<script>
 import { searchByLabelCode } from '@/request/api/material'
	export default{
		data(){
			return{
				label_code:'',
				mate_list:'',
				days:'',
				isDisabled:false
			}
		},
		methods:{
			search(){
				let _this = this				
				this.isDisabled = true
				searchByLabelCode({label_code:this.label_code}).then(res =>{
					if (res.result) {
						if (Object.keys(res.mate_info).length ==0) {
							this.$message(this.$t('common.nodata'));
							this.mate_list = ''
						}else {
							this.mate_list = res.mate_info;
						}
					} else {
						this.mate_list = ''
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
					}
			})
				let Timer=window.setTimeout(function(){
					_this.isDisabled = false
				},1500)
			}	
		}
	}
</script>
<style >
	.pda_search{
		margin-top:0.2rem; 
		display: flex;
		text-align: left;
	}
	.code_span{
		display: block;
		width:3rem;
		height: 1.06rem;
		line-height: 1.06rem;
		font-size: 0.4rem;
	}
	.pdasearch_input{
		height: 1.06rem;
		line-height: 1.06rem;
		font-size: 0.4rem;
	}
	.pda_selected_div .el-input__inner{
		height: 1.06rem;
		line-height: 1.06rem;
		font-size: 0.4rem;
	}
	.pda_selected_div .el-input{
		height: 1.06rem;
		line-height: 1.06rem;
		font-size: 0.4rem;
	}
	.pdasearch_btn {
	width:100%;
	height: 1.06rem;
	}
	.labeltable {
  border-collapse: collapse;
  margin: 0 auto;
  text-align: center;
}
.table_div{
	margin-top: 0.53rem;
}
.labeltable{
	width:100%;
}
	.labeltable td{
		border:0.026rem solid lightgray;
		height: 0.85rem;
		line-height: 0.85rem;
	}
.table_div	.title_td{
		width:40%;
	}
.table_div .content_td{
		width:60%;
	}
</style>


